    _positionAscendingComparer: function _positionAscendingComparer(x, y) {
      return x.k < y.k ? -1 : x.k > y.k ? 1 : 0;
    },

    _positionDescendingComparer: function _positionDescendingComparer(x, y) {
      return x.k < y.k ? 1 : x.k > y.k ? -1 : 0;
    },

